import React from 'react';
import PropTypes from 'prop-types';
import { convertPropToCssClassName } from '@elliemae/ds-classnames';

const DSCardHeader = ({ title, action }) => {
  const { cssClassName, classNameElement } = convertPropToCssClassName(
    'card-header',
  );
  return (
    <div className={cssClassName} data-testid="card-header">
      <span className={classNameElement('heading')}>{title}</span>
      <div
        className={classNameElement('pull-right')}
        data-testid="ds-card_header-action"
      >
        {action}
      </div>
    </div>
  );
};

DSCardHeader.propTypes = {
  title: PropTypes.string,
  action: PropTypes.node,
};

export default DSCardHeader;
